@mixin for-size($size) {
    @if $size ==phone {
        @media (max-width: 767px) {
            @content;
        }
    }

    @else if $size ==tablet {
        @media (min-width: 768px) {
            @content;
        }
    }

    @else if $size ==desktop {
        @media (min-width: 1200px) {
            @content;
        }
    }

}